<template>
  <section id="myScroll" class="scroll center-team-news w-full  bg-white p-2 border border-1 border-slate-400 rounded-lg" :style="{height: heightBox+'px'}">
    <div class="container mx-auto p-2">
      <div class="w-full h-auto">
        <p class="font-bold text-sm text-black text-start">آخرین اخبار تیم ایران</p>
        <div class="bg-black w-full h-0.5 mb-2"></div>
        <div class="flex flex-col space-y-2">
          <div v-for="items in myList" :key="items.news_id">
            <div v-for="value in items" :key="value.news_id">
              <div class="flex flex-row justify-between mb-3 p-2">
                <div class="grid grid-cols-1 space-y-5 w-8/12" :style="{height:heightText+'px'}">
                  <div class="w-full">
                    <p class="text-center font-semibold text-slate-800 text-right" :style="{fontSize:heightFont+'px'}">{{value.title}}</p></div>
                  <div class="flex flex-row justify-between w-full">
                    <div>
                      <p class="text-right text-md font-semibold text-slate-500">4 روز قبل</p>
                    </div>
                    <div class="flex flex-row">
                      <font-awesome-icon :icon="['fas','comment']" class="text-slate-500 text-sm mx-1"></font-awesome-icon>
                      <span class="text-sm text-slate-500">{{value.news_comment_count}}</span>
                      <font-awesome-icon :icon="['fas','eye']" class="text-sm text-slate-500 mx-1"></font-awesome-icon>
                      <span class="text-slate-500 text-sm mx-1">{{value.visit}}</span>
                    </div>
                  </div>
                </div>
                <div class="myImage w-3/12 h-full">
                  <img :src="`${value.news_image[0].url}`" alt="" class="w-full h-full">
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import{ref} from 'vue'
export default {
  name: "ExampleTeamNews",
  props:{
    myList:{
      type:Array,
      required:true
    }
  },
  components:{FontAwesomeIcon},
  setup(){
    const heightBox=ref(1000)
    const heightFont=ref(20)
    const heightText=ref(150)
    return{
      heightText,heightBox,heightFont
    }
  }
}
</script>

<style scoped>
.scroll{
  overflow-y: scroll;
  overflow-x: hidden;
}
#myScroll::-webkit-scrollbar{
  width:5px;
  background-color: #fff;
}
#myScroll::-webkit-scrollbar-thumb{
  background-color: gray;
}
#myScroll::-webkit-scrollbar-track{
  background-color: lightgray;
  box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}
</style>