<template>
<section class="center-team-rank">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <div class="flex flex-row align-center justify-between">
        <div class="flex flex-row">
          <p class="font-bold text-black text-sm text-start my-2">جدول رده بندی</p>
          <select name="selected" class="outline-none border-0">
            <slot name="rankName"><option class="font-bold text-slate-900 text-sm text-start">انتخابی جام جهانی</option></slot>
            <option class="font-bold text-slate-900 text-sm text-start">جام جهانی 2022</option>
          </select>
        </div>
        <div class="flex flex-row">
          <span class="font-semibold text-slate-700 text-sm text-start">بیشتر</span>
          <font-awesome-icon :icon="['fas','angle-left']" class="text-sm text-slate-700 font-semibold ml-3 p-1"></font-awesome-icon>
        </div>
      </div>

      <div class="flex flex-row justify-end my-1">
        <span class="font-semibold text-slate-700 text-sm text-start">بازی های اخیر</span>
        <input type="checkbox" class="text-sm font-semibold text-slate-700">
      </div>
      <div class="flex flex-row justify-end my-1">
        <ul v-for="nums in total" :key="nums">
          <li class="px-1 text-sm text-slate-300">{{nums}}</li>
        </ul>
      </div>
      <div class="bg-slate-900 w-full h-0.5 my-2"></div>
      <div v-for="items in myList" :key="items.id">
        <div v-for="value in items" :key="value.id">
          <div v-for="(myVal,index) in value.standings" :key="myVal.id">
            <table class="table w-full">
              <tr class="flex flex-row justify-between my-4 w-full">
                <td class="flex flex-row w-4/12">
                  <span class="text-black">{{index+=1}}</span>
                  <img :src="`${myVal.team.logo}`" alt="" class="rounded-full w-9 h-9 mx-1">
                  <span class="text-right font-semibold text-slate-800 mx-1">{{myVal.team.name_fa}}</span>
                </td>
                <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.match_played}}</span></td>
                <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.win}}</span></td>
                <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.draw}}</span></td>
                <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.lose}}</span></td>
                <td class="flex flex-row w-1/12"><span class="text-sm text-slate-800">{{myVal.goal_scored - myVal.goal_against}}</span></td>
                <td class="flex flex-row w-1/12">
                  <span class="text-sm text-slate-800">{{myVal.goal_against}}</span>
                  <b>-</b>
                  <span class="text-sm text-slate-800">{{myVal.goal_scored}}</span>
                </td>
                <td class="flex flex-row w-1/12">
                  <span class="text-sm text-slate-800">{{myVal.overall_point}}</span>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {ref,reactive} from 'vue'
export default {
  name: "ExampeTeamRank",
  props:{
    myList:{
      type:Array,
      required:true
    }
  },
  components:{FontAwesomeIcon},
  setup(){
    const size=ref(10)
    const rightText=ref(10)
    const total=reactive(['بازی','برد','مساوی','شکست','تفاضل','گل ها','امتیاز'])
    return{
      size,rightText,total
    }
  }
}
</script>

<style scoped>

</style>